/*
(),.

\/:*?<>|
()255

: [^\s\\/:\*\?\"<>\|]
: [^\s\\/:\*\?\"<>\|\.]
: (\x20|[^\s\\/:\*\?\"<>\|])*

*/



export function isValidFileName (fileName: string) {
  if (!fileName && fileName.length <= 255) {
    return false
  } else {
    const regExp = /[^\s\\/:*?"<>|]?(\x20|[^\s\\/:*?"<>|])*[^\s\\/:*?"<>|.]$/;
    return fileName.match(regExp)
  }
}