











import { Component, Prop, Vue } from 'vue-property-decorator'
import TabInfo from '@/components/application-editor/object-editor-tabs/TabInfo';
import SettingEditableObject from '@/core/editable-object/setting/SettingEditableObject'
import CodeEditor from '@/components/common/code-editor/CodeEditor.vue'

@Component({
  components: { CodeEditor }
})
export default class SettingEditor extends Vue {
  @Prop({ required: true })
  protected tabInfo!: TabInfo;

  private setting = "123"

  private get settingEditableObject() {
    return this.tabInfo.object as SettingEditableObject
  }
}
