







































import { Component, Vue } from 'vue-property-decorator'

export interface MeunItem {
  name: string;
  disable?: boolean;
  handler?: ((event: MouseEvent) => void) | (() => void);
}

@Component
export default class ApplicationEditorMenu extends Vue {
  private contextmenu: {
    opened: boolean;
    x: number;
    y: number;
    event: MouseEvent | null;
    meunItems: MeunItem[];
  } = {
    opened: false,
    x: 0,
    y: 0,
    event: null,
    meunItems: []
  }

  public openContextmenu (event: MouseEvent, meunItems: MeunItem[]) {
    const contextmenu = this.contextmenu
    const clientHeight = document.body.clientHeight
    const clientWidth = document.body.clientWidth
    const maxX = clientWidth - 350
    const maxY = clientHeight - meunItems.length * 32
    contextmenu.opened = true
    contextmenu.x = Math.min(event.clientX, maxX)
    contextmenu.y = Math.min(event.clientY, maxY)
    contextmenu.event = event
    contextmenu.meunItems = meunItems
  }

  public closeContextmenu () {
    const contextmenu = this.contextmenu
    contextmenu.opened = false
    contextmenu.x = 0
    contextmenu.y = 0
    contextmenu.meunItems = []
  }
}
