

















import { Component, Prop, Vue, Inject } from 'vue-property-decorator'
import PageConfig from '@/core/model/PageConfig';
import WidgetWrapper from './WidgetWrapper.vue'
import ApplicationEditor from '../ApplicationEditor.vue';

@Component({
  components: { WidgetWrapper }
})
export default class VueFilePreviewer extends Vue {
  @Inject('app-editor')
  public appEditor!: ApplicationEditor

  @Prop()
  private pageConfig!: PageConfig;

  private get widgetConfigs() {
    return this.pageConfig.widgetConfigs
  }

  public getComponent(widgetTag: string) {
    return this.appEditor.getWidgetComponent(widgetTag)
  } 
}
