



































import { Component, Prop, Vue } from 'vue-property-decorator'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

@Component({
  components: {
    FontAwesomeIcon
  }
})
export default class TxtEditor extends Vue {
  @Prop({ type: String, required: true })
  private value!: string;

  @Prop({ type: String, required: false, default: '' })
  private editBtnTitle!: string;

  @Prop({ type: String, required: false, default: '' })
  private defaultText!: string;

  @Prop({ type: String, required: false, default: 'button' })
  private triggerMode!: string // button dbclick

  @Prop({ type: String, required: false, default: '' })
  private placeholder!: string;

  private innerValue = '';
  private inputShowed = false;

  public startInput () {
    this.inputShowed = true
    this.$nextTick(() => {
      this.innerValue = this.value;
      (this.$refs.input as HTMLElement).focus()
    })
  }

  private handleIconBtnClick () {
    this.startInput()
  }

  private handleDbclick() {
    if (this.triggerMode === 'dbclick') {
      this.startInput()
    }
  }

  private inputValue () {
    this.inputShowed = false
    this.$emit('input', this.innerValue || this.defaultText);
    (this.$el as HTMLElement).scrollTo(0, 0)
  }
}
